import logo from './logo.svg';
import './App.css';
import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  Title,
  Tooltip,
  Legend
} from "chart.js";

ChartJS.register(
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement,
  Title,
  Tooltip,
  Legend
);

function App() {
  const [sensorData, setSensorData] = useState({
    co2: 0,
    pm25: 0,
    temp: 0,
    humidity: 0,
    noise: 0,
    timestamp: ""
  });

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await fetch("http://192.168.86.20:5050/api/data/latest");
        const data = await res.json();
        setSensorData(data);
      } catch (err) {
        console.error("Failed to fetch sensor data", err);
      }
    };

    fetchData();
    const interval = setInterval(fetchData, 10000);
    return () => clearInterval(interval);
  }, []);

  const lineData = (label, value) => ({
    labels: ["Now"],
    datasets: [
      {
        label,
        data: [value],
        borderWidth: 2,
        fill: false
      }
    ]
  });

  return (
    <div style={{ padding: "2rem", fontFamily: "Arial, sans-serif" }}>
      <h1 style={{ fontSize: "2rem", fontWeight: "bold" }}> BloomAir Live Dashboard</h1>
      <p>Supporting pollinators through eco-friendly, sensor-driven habitats.</p>

      <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: "1rem", marginTop: "1rem" }}>
        <div style={{ border: "1px solid #ccc", padding: "1rem", borderRadius: "1rem" }}>
          <h2>CO Levels (ppm)</h2>
          <Line data={lineData("CO", sensorData.co2)} />
        </div>
        <div style={{ border: "1px solid #ccc", padding: "1rem", borderRadius: "1rem" }}>
          <h2>PM2.5 (g/m)</h2>
          <Line data={lineData("PM2.5", sensorData.pm25)} />
        </div>
        <div style={{ border: "1px solid #ccc", padding: "1rem", borderRadius: "1rem" }}>
          <h2>Temperature (C)</h2>
          <Line data={lineData("Temperature", sensorData.temp)} />
        </div>
        <div style={{ border: "1px solid #ccc", padding: "1rem", borderRadius: "1rem" }}>
          <h2>Humidity (%)</h2>
          <Line data={lineData("Humidity", sensorData.humidity)} />
        </div>
        <div style={{ border: "1px solid #ccc", padding: "1rem", borderRadius: "1rem" }}>
          <h2>Noise (dB)</h2>
          <Line data={lineData("Noise", sensorData.noise)} />
        </div>
      </div>

      <div style={{ marginTop: "2rem" }}>
        <h2>Community Impact</h2>
        <p>
          Track and share your own pollinator-friendly efforts: DIY shelters, native gardens,
          or pollinator sightings. Together we can grow a data-backed movement for biodiversity!
        </p>
        <button style={{
          marginTop: "1rem",
          padding: "0.5rem 1rem",
          backgroundColor: "#28a745",
          color: "#fff",
          border: "none",
          borderRadius: "0.5rem",
          cursor: "pointer"
        }}>
          Log My Contribution
        </button>
      </div>
    </div>
  );
}

export default App;
